


@media(min-width:768px){

 .image{
    width:20em;
    height:inherit;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
  }
  .introduction img {
    height: 35em;
    transform: translateX(-12em);
  }
  .client img{
    height: 30em;
    transform:translateX(-8em) translateY(-0em);
  }
  .ideation img{
    height:30em;
    transform:translateX(-15em);

  }
  .prototype img{
    height: 32em;
    transform:translateX(-1em) translateY(-1em)

  }
}

@media(max-width:768px){

  .image{
    height: 30vh;
    width:65vw;
    object-fit: cover;
    overflow: hidden;
    border-radius: 1em;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
  }
  .introduction img {
    width:150%;
    bottom: 0;
    transform:translateY(-6vh);
  }  
}