
.content {
  position: absolute;
  max-height: 85vh;
  top:15vh;
  z-index: 10;
  }
  
  
  /* Change the color of the scrollbar thumb */
    .content::-webkit-scrollbar-thumb {
      background-color: rgb(187, 38, 38);
    }
    
    /* Change the color of the scrollbar track */
    .content::-webkit-scrollbar-track {
      background-color: rgb(87, 4, 4);
    }

@media(min-width:768px){
.content {
  max-width: 100em;

    display: grid;
    align-items: center;
    gap: 5em;
    margin-bottom: 30vh;

      overflow-y: scroll;
      scroll-snap-type: y mandatory;
    
  }

  
  
  .content-block {
    height:30em;
    width:90%;

    z-index: 10;
    
    display: grid;
    grid-template-columns: 20em 1fr;

    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
    border: 0.3em solid white;
    border-radius: 2em;
    background-color: rgba(126, 126, 126, 0.048);
  }
 .image{
    width:20em;
    height:inherit;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
  }
  
  .navbar-background,
  .content::before {
    height: 35vh;
    width:45%;
    bottom:-100px;
    right: 10%;
    position: fixed;
  }
  .text{
    width:60%;
    margin:15%;
    margin-left: 20%;
    text-align: center;
    overflow: hidden;
  }
}

  @media(max-width:768px){
    .content{
      width:100vw;
      height: 80vh;
  
      top:15vh;
      align-items: center;

      display: flex;
      overflow-y: hidden;
      overflow-x: scroll;
      scroll-behavior: smooth;
      z-index: 100;
    }
    
    
    
    .content-block{
      height:40em;
      padding:2em;
      padding-top: 0;
      padding-bottom: 0;
      border-radius: 2em;
      margin: 5vw;
      margin-top: 0;
      width:100vw;

      position: relative;
      
      z-index: 10;
      display: grid;
      grid-template-rows: 20em 1fr;
      scroll-margin-left: 20em;
      scroll-margin-right: 20em;

      background-color: rgba(250, 235, 215, 0);
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
      border: 0.3em solid white;
      scroll-snap-align: start;

    }
    .image{
      height: 30vh;
      width:65vw;
      object-fit: cover;
      overflow: hidden;
      border-radius: 1em;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
    }
    
    
    .navbar-background {
      height: 12em;
      width:45%;
      bottom:-100px;
      right: 10%;
      position: fixed;
    }

    .text{
      width:70%;
      margin:15%;
      margin-top: 0;
      text-align:justify;
      overflow: hidden;
    }


  }
  
 



  
  .title{
    width:100%;
    text-align: center;
    height:10vh;
  }



  
         
  .navbar-background{

    background-color:rgba(240, 240, 240, 0.9);
    z-index: 15;
    position: fixed;

    border-radius: 0.4em;
    box-shadow: 0 4px 40px rgba(0, 0, 0, 0.13);
  }



  .content::before {
    content: "";
    backdrop-filter: blur(2px); /* Adjust blur amount as needed */
    z-index: -1; /* Make sure this is below the .navbar-background */
  }





  